<template>
<div id="box">
  <div class="orderDetails">
    <div class="head">
      <div class="title">{{ info.order_status_detail || '无' }}</div>
    </div>
    <div class="logistics">
      <div class="item">
        <p class="title">订单编号： {{info.shipping_name}}  {{info.order_sn}}</p>
        <p class="time" style="display:inline-block;padding-right:50pt;">{{info.shipping?info.shipping:'暂无物流信息'}}</p>
      </div>
    </div>
    <div class="wrap" >
      <div class="msg"  style='padding:5pt;'>
        <h4 class="msg1">商品信息</h4>
        <div class="product flex" v-for="(item,index) in info.order_goods" :key='index'>
          <img :src="item.original_img" alt="">
          <div class="info flex_1">
            <p class="title">{{item.goods_name}}</p>
            <p class="price">￥{{item.final_price}} &nbsp;&nbsp;&nbsp;&nbsp;<span> X{{item.goods_num}}</span></p>
          </div>
        </div>
      </div>
      <div class="list">
        <h4>收货地址</h4>
        <div class="item flex">
          <div class="title">收件人:</div>
          <div class="text"> &nbsp;&nbsp;&nbsp;&nbsp;{{info.consignee}}</div>
        </div>
        <div class="item flex">
          <div class="title">电话号码：</div>
          <div class="text"> &nbsp;&nbsp;&nbsp;&nbsp;{{info.mobile}}</div>
        </div>
        <div class="item flex">
          <div class="title">收货地址：</div>
          <div class="text"> &nbsp;&nbsp;&nbsp;&nbsp;{{info.address}}</div>
        </div>
      </div>
      <div class="list">
        <h4>订单信息</h4>
        <div class="item flex-between">
          <div class="title">订单编号：</div>
          <div class="text">{{info.order_sn}}</div>
        </div>
        <div class="item flex-between">
          <div class="title">下单时间：</div>
          <div class="text">{{date(info.add_time)}}</div>
        </div>
        <div class="item flex-between">
          <div class="title">支付方式：</div>
          <div class="text">{{info.pay_name}}</div>
        </div>
        <div class="item flex-between">
          <div class="title">备注：</div>
          <div class="text" style="color: #868686">{{info.user_note?info.user_note:'无'}}</div>
        </div>
      </div>
      <div class="list">
        <div class="item flex-between">
          <div class="title">商品金额：</div>
          <div class="text">￥{{info.goods_price}}</div>
        </div>
        <div class="item flex-between">
          <div class="title">运费：</div>
          <div class="text">+￥{{info.shipping_price}}</div>
        </div>
        <div class="item flex-between">
          <div class="title">优惠券抵扣：</div>
          <div class="text">-￥{{info.coupon_price}}</div>
        </div>
        <!-- <div class="item flex-between">
          <div class="title">余额抵扣：</div>
          <div class="text">-￥{{info.user_money}}</div>
        </div>
        <div class="item flex-between">
          <div class="title">积分抵扣：</div>
          <div class="text">-￥{{info.integral_money}}</div>
        </div> -->
        <div class="item flex-between">
          <div class="title">实付金额：</div>
          <div class="text" style="color: #e22424">￥{{info.order_amount}}</div>
        </div>
      </div>
    </div>
    <div class="handle flex-center-y">
      <div class="border flex-center" @click="kefu()">联系客服</div>
      <!-- <div class="border flex-center">取消订单</div> -->
      <!-- <div class="background flex-center">付款</div> --> 
      <!-- <div class="background_blue flex-center" v-if="info.shipping_status" @click="getsure()">确认收货</div> -->
    </div>
  </div>
</div>
</template>

<script>
import util from '../utils/utils.js'   
import { NavBar, Dialog } from "vant";
import { getorder_detail, mysure, kefu } from '@/axios/getData'
export default {
  data() {
    return {
      data: {
        order_goods: [{}]
      },
      info:{}
    }
  },
  created() {
    this.getdata();
    console.log(this.$route.query.id)
  },  
  methods: {
    async getdata(){
      var res = await getorder_detail({id:this.$route.query.id});
      this.info = res.data
      console.log(res.data)
    },
    //确认收货
    async getsure(){
      mysure({order_id:this.info.order_id}).then((res)=>{
        if(res==200){
          Toast('确认收货成功');
          this.$route.go(-1);
        }
      })
    },
    //联系客服
    kefu(){
      var that = this;
      kefu().then(res =>{
        Dialog.confirm({
          title: res.data.title,
          message: res.data.number
        }).then(() => {
          // 不加回报异常          
        }).catch(() =>{
          // 不加回报异常
        })
      })
    },
    date(val) {
      if (val) {
        let date = util.format(val*1000)
        return date
      }
    }
  },
  components:{
    vanNavBar:NavBar
  }
}
</script>

<style scoped lang="scss">
.orderDetails {
  background: url('src/assets/images/矩形10拷贝3@2x.png') no-repeat top;
  background-size: 100% auto;
  overflow: hidden;
  padding: 0 35px;
  background-color: #fff;
}
.head {
  padding: 40px 0;
  color: #fff;
  .title {
    font-size: 46px;
    margin-bottom: 14px;
  }
}
.logistics {
  padding: 0 30px;
  box-shadow: 0 4px 20px #e6e6e6;
  background: #fff;
  border-radius: 14px;
  margin-bottom: 30px;
  .item {
    background: url(src/assets/images/图层18@2x.png) no-repeat 100% center;
    padding: 30px 0;
    &:first-child {
      border-bottom: 1px solid #e1e1e1;
    }
    .time {
      margin-top: 15px;
      color: #848484;
      font-size: 26px;
    }
    .address {
      margin-top: 15px;
      color: #4c4c4c;
      font-size: 26px;
    }
  }
}
.wrap {
  background: #fff;
  margin-bottom: 100px;
  border-radius:6pt !important; 
  .lepin{
    margin-bottom:50px;
    .inner{
      margin:20px 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .button {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 150px;
          height: 60px;
          border-radius: 60px;
          border: 2px solid #eee;
          color: #848484;
      }
    }
  }
  .msg{
    .msg1{
      font-size: 0.48rem;
      font-weight: 100;
      color: #333;
    }
  }
  .product {
    padding: 25px 0;
    border-bottom: 1px solid #eee;
    img {
      width: 120px;
      height: 120px;
      border-radius: 10px;
    }
    .info {
      overflow: hidden;
      padding-left: 20px;
      .title {
        font-size: 30px;
        margin-bottom: 8px;
      }
      span {
        color: #848484;
      }
      .afterSale {
        justify-content: flex-end;
        .button {
          width: 150px;
          height: 60px;
          border-radius: 60px;
          border: 2px solid #eee;
          color: #848484;
        }
      }
    }
  }
  .list {
    padding: 40px 0;
    border-bottom: 1px solid #eee;
    h4 {
      padding-left:5pt;
      font-size: 36px;
      font-weight: 100;
      margin-bottom: 30px;
      color: #333;
    }
    .item {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
      .title {
        color: #868686;
      }
      .text {
        color: #020202;
      }
    }
  }
}
.handle {
  height: 100px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0 0 10px #ece8e8;
  justify-content: flex-end;
  div {
      width: 170px;
      height: 50px;
      border-radius: 30px;
      margin-right: 20px;
      font-size: 28px;
    }
    .border {
      box-sizing: border-box;
      color: #545454;
    }
    .background {
      background: linear-gradient(to right, #fcd54e , #edb342);
      color: #fff;
    }
    .background_blue  {
      background: linear-gradient(to left, #F71F40 , #FF6630);
      color: #fff;
    }
}
.margin_left{
  margin-left: -15pt;
}
.aa{
  width:27pt;
  height:27pt;
  border-radius: 50%;
}
.s19_5{
  width:19pt;
  height:5pt;
}
</style>




